.about-us__text {
  color: #ffffff;
  display: block;
  -webkit-margin-before: 1em;
  -webkit-margin-after: 1em;
  -webkit-margin-start: 0px;
  -webkit-margin-end: 0px;
  margin-right: 400px;
}
.about-us__header {
  color: #ffffff;
  text-transform: uppercase;
  max-width: 400px;
  font-size: 38px;
  font-family: "Proxima-Nova-Bold", sans-serif;
  font-weight: normal;
  line-height: normal;
  -webkit-font-smoothing: antialiased;
  margin: 0.67em 0;
  -webkit-margin-before: 0.83em;
  -webkit-margin-after: 0.83em;
  -webkit-margin-start: 0px;
  -webkit-margin-end: 0px;
}

.section--full-height {
  height: 100vh;
}
.section--home {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
  align-items: center;
  background-image: -webkit-linear-gradient(230deg, #2c7dbc 15%, #7cccc5 70%);
  background-image: -o-linear-gradient(230deg, #2c7dbc 15%, #7cccc5 70%);
  background-image: linear-gradient(-140deg, #2c7dbc 15%, #7cccc5 70%);
}
.section--home .section__layout {
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
  align-items: center;
  -ms-flex-pack: justify;
  justify-content: space-between;
}
.section--home .section__content {
  z-index: 1;
	width: 100%;
}
.home-circle {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 0;
  width: 100%;
  height: 100%;
  background-image: url("img/home-circle.svg");
  background-position: 100%;
  background-repeat: no-repeat;
  opacity: 0.8;
  margin-right: 400px;
}
@media screen and (min-width: 1024px) {
  .section__layout {
    max-width: 960px;
    margin: 0 auto;
  }
}

#main-logo {
    z-index: 2;
    display: block;
    margin-right: 30px;
}

#main-logo img {
  width: 200%;
	border-radius: 300%;
	-webkit-box-shadow: rgba(0,0,0,0.9);
	        box-shadow: rgba(0,0,0,0.9);
	margin-right: 20%;

}

.mega_layout {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}



